import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './button.scss';

const Button = ({ width, path, text, setTitle }) => {
  const [isTablet, setIsTablet] = useState(false);

  const handleSetTitle = (text) => {
    setTitle(text);
  };

  const resize = () => {
    let currentView = window.innerWidth <= 1199;
    if (currentView !== isTablet) {
      setIsTablet(currentView);
    }
  };

  useEffect(() => {
    window.addEventListener('resize', resize.bind(this));
    resize();

    return window.removeEventListener('resize', resize.bind(this));
  });

  if (isTablet) {
    return (
      <div className='btn-container' style={{ width }}>
        <Link to={path} className='btn btn-effect'>
          <span>{text}</span>
        </Link>
      </div>
    );
  }
  return (
    <div className='btn-container' style={{ width }}>
      <Link
        to={path}
        className='btn btn-effect'
        onClick={() => handleSetTitle(text)}
      >
        <span>{text}</span>
      </Link>
    </div>
  );
};
export default Button;
